import React,{Component} from 'react'
import {View,StyleSheet,Text, TouchableOpacity} from 'react-native'
import MenuHeader from "../../menu/menuheader.js"
import Ptext from "../../../common/Ptext.js"
import TabAbout from "../user/tabAbout.js"
import TabReview from "../user/tabReview.js"
import TabSettings from "../user/tabSettings.js"
import ImagePicker from 'react-native-image-crop-picker';


class ProfileUser extends Component {
  state = {
    menu : [
      {
        title : ' ',
        cb : this._tttt,
        selected : true,
        url : 'user',
      },
      {
        title : '',
        cb : this._tttt,
        selected : false,
        url : 'notify',
      },
      {
        title : '',
        cb : this._tttt,
        selected : false,
        url : 'orders',
      },
    ],
    selected : 0,
    user : {
      FirstName : '',
      LastName : ''
    }
  }
  _tttt = () => {

  }

  _selectcb = (index) => {
    let menu = this.state.menu;
    for (let i in menu)
      menu[i].selected = false;
    menu[index].selected = true;
    this.setState({menu : menu,selected : index});
  }

  _openEditFullName = () =>  {
    let formData = {
        caption : '  ',
        mainHint : '      .      ',
        items : [
          {
            caption : '',
            placeholder : '  ',
            multi : false,
            secret : false,
            value : this.state.user.FirstName,
          },
          {
            caption : '',
            placeholder : '  ',
            multi : false,
            secret : false,
            value : this.state.user.LastName,
          },


        ],
        titleSubmit : '',

      }
      this.props.cbOpenEditForm(formData);
  }

  _onChangePasswordPress = () => {
    let formData = {
        caption : ' ',
        mainHint : '',
        items : [
          {
            caption : ' ',
            placeholder : '   ',
            multi : false,
            secret : true,
            value : '',
          },
          {
            caption : ' ',
            placeholder : '   ',
            multi : false,
            secret : true,
            value : '',
            hint : '        ,     5     .',

          },
          {
            caption : '   ',
            placeholder : '',
            multi : false,
            secret : true,
            value : '',
          },
        ],
        titleSubmit : '',

      }
      this.props.cbOpenEditForm(formData);
  }

  render (){
  return (
      <View style={styles.wrapper}>
        <MenuHeader editMode={true} style={{width : window.innerWidth}} onAvatarPress={this.props.openImagePicker}
            cbCloseMenu={this._openEditFullName} userName={this.state.user.FirstName + ' ' + this.state.user.LastName}/>
        <View style={styles.horizontal}>
        {
          this.state.menu.map((item,index,arr) => {
            return (
            <TouchableOpacity onPress={this._selectcb.bind(this,index)}>
                <Ptext style={[styles.title_menu,{color : 'black', marginBottom : 3}]}>{item.title}</Ptext>
                <View style={{width : '90%',
                    borderTopWidth : item.selected ? 2 : 0,
                    height : 7,
                    borderTopColor : '#45b4e3',
                    alignSelf : 'center'}}/>
            </TouchableOpacity>
          );
          })
        }
        </View>
        {
          this.state.selected === 0 ?
          <TabAbout/>
          : this.state.selected === 1 ?
          <TabReview/>
          :
          <TabSettings onChangePasswordPress={this._onChangePasswordPress}/>
        }
      </View>
    )
  }


}

const styles = StyleSheet.create({
  wrapper : {
    flexDirection : 'column',
    alignSelf : 'center',
    justifyContent : 'flex-start',
    alignItems : 'flex-start',
    width : '100%',
    backgroundColor : '#fff',
  },
  horizontal : {
    flexDirection : 'row',
    width : '100%',
    alignItems : 'center',
    alignSelf : 'flex-start',
    justifyContent : 'flex-start',
    backgroundColor : '#fff',
    marginBottom : 8,
  },
  title_menu : {
    fontSize : 16,
    lineHeight : 24,
    textAlign : 'left',
    fontWeight : 'normal',
    margin : 18,
  },

});

export default ProfileUser;
